import React from 'react'
import { Accordion, Container } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import Breadcrumbs from '../../../../ReusableComponents/breadcrumbs/Breadcrumbs';

const Jan = () => {
    const { t } = useTranslation();
    return (
        <Container style={{ backgroundColor: '#fff', display: 'flex', flexDirection: 'column', alignItems: 'center', padding: 80, borderRadius: '8px' }}>
            <Breadcrumbs />
            <Accordion defaultActiveKey="0" flush style={{ width: '80%', border: '1px solid black' }}>
                <Accordion.Item eventKey="0">
                    <Accordion.Header>14 {t('jan')}</Accordion.Header>
                    <Accordion.Body>
                             1850  14 ,        .
                                 .
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey='1'>
                    <Accordion.Header>26 {t('jan')}</Accordion.Header>
                    <Accordion.Body>
                       (World Day of the writer)  3    
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey='2'>
                    <Accordion.Header>31 {t('jan')}</Accordion.Header>
                    <Accordion.Body>
                    31      -    -  , 
                        .    - - ,     .
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
        </Container>
    )
}

export default Jan