import React from 'react'
import { Container } from 'react-bootstrap';
import Breadcrumbs from '../../../ReusableComponents/breadcrumbs/Breadcrumbs';
import PartnersCard from '../../../ReusableComponents/partners/PartnersCard';
import { useTranslation } from 'react-i18next';

import jan from "../../../assets/months/jan.png"
import feb from "../../../assets/months/feb.png"
import mar from "../../../assets/months/mar.png"
import apr from "../../../assets/months/Apr.png"
import may from "../../../assets/months/may.png"



const Calendar = () => {
  const { t } = useTranslation();
  return (
    <Container style={{ backgroundColor: '#fff', padding: '20px 20px', borderRadius: '8px' }}>
      <Breadcrumbs />
      <div style={{ display: "flex", justifyContent:'center',  flexWrap: 'wrap', gap: '30px 4%'}}>
        <PartnersCard name={t('')} thumb={jan} img={jan} link={'jan'} />
        <PartnersCard name={t('')} thumb={feb} img={feb}  link={'feb'} />
        <PartnersCard name={t('')} thumb={mar} img={mar} link={'mar'} />
        <PartnersCard name={t('')} thumb={apr} img={apr} link={'Apr'} />
        <PartnersCard name={t('')} thumb={may} img={may} link={'May'} />
        <PartnersCard name={t('')} thumb={jan} img={jan} link={'June'} />
        <PartnersCard name={t('')} thumb={feb} img={feb} link={'July'} />


        <PartnersCard name={t('')} thumb={mar} img={mar} link={'May'} />
        <PartnersCard name={t('')} thumb={apr} img={apr} link={'May'} />
        <PartnersCard name={t('')} thumb={may} img={may} link={'May'} />
        <PartnersCard name={t('')} thumb={may} img={may} link={'May'} />
        <PartnersCard name={t('')} thumb={may} img={may} link={'May'} />
      </div>
    </Container>
  )
}

export default Calendar           